ition: 0.5s;\r\n  }\r\n  .right {\r\n    transform: translateX(80%) scale(0.83);\r\n    opacity: 0.75;\r\n    z-index: -2;\r\n    transition: 0.5s;\r\n  }\r\n  .topname_p {\r\n    font-size: 0.5em !important;\r\n  }\r\n  .top5phone_p {\r\n    font-size: 0.7em !important;\r\n  }\r\n  .top5add_p {\r\n    font-size: 0.1em !important;\r\n  }\r\n}\r\n.category_box {\r\n  display: inline-block;\r\n  margin-left: 1em;\r\n}\r\n\r\n@media screen and (max-width: 350px) {\r\n  .category_box {\r\n    margin: 0;\r\n  }\r\n  .topname_p {\r\n    font-size: 0.5em !important;\r\n  }\r\n  .top5phone_p {\r\n    font-size: 0.7em !important;\r\n  }\r\n  .top5add_p {\r\n    font-size: 0.1em !important;\r\n  }\r\n}\r\n"],"sourceRoot":""}]);
